	data() {
		return {
			popmenuShow : true,
			popmenuShowX : true
		};
	},
	methods: {
		showmenu : function () {
			this.popmenuShow = !this.popmenuShow;
		},
		showmenuX : function () {
			this.popmenuShowX = !this.popmenuShowX;
		}
	},
}
